.wrapper {
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;

	&.loaded {}
}

body._lock {
	overflow: hidden;
}

//sticky
.content {
	flex: 1 1 auto;
}

