//<ÐžÐ¡ÐÐžÐ’ÐÐÐ¯ Ð¡Ð•Ð¢ÐšÐ>===========================================================================================================
.container {
	max-width: $mw+px;
	margin: 0 auto;
	width: 100%;
	padding: 0 10px;
}

