// education

.education {
	padding: 100px 0px 60px;
	background: linear-gradient(-211deg, $tabs-bg 0%, #fdfde0 100%);

	// .education__container
	&__container {
		overflow: hidden;
		padding: 0 10px;
	}

	// .education__title
	&__title {
		max-width: 810px;
		margin: 0px auto;

		.title__item {
			margin: 30px auto 0;
			max-width: 533px;
		}
	}

	// .education__label
	&__label {
		font-weight: 700;
		line-height: 27px;
		font-size: 22px;
		text-align: center;
		margin: 50px 0px;

		@media (max-width: $md4+px) {
			margin: 40px 0px 25px 0px;
		}
	}

	// .education__row
	&__row {
		display: flex;
		margin: 0px -41px;
		flex-wrap: wrap;

		@media (max-width: $md2+px) {
			justify-content: center;
		}
	}

	// .education__column
	&__column {
		flex: 0 0 25%;
		padding: 0px 40px;
		position: relative;
		margin: 0px 0px 40px 0px;

		&:after {
			content: '';
			background: url("../../dist/img/icons/right-drawn-arrow-icon.png") center no-repeat;
			display: block;
			width: 64px;
			height: 24px;
			position: absolute;
			top: 24%;
			right: 0;
			transform: translate(50%, 0px);
		}

		&:last-child:after {
			background: none !important;
		}

		@media (max-width: $md2+px) {
			flex: 0 0 50%;
		}

		@media (max-width: $md4+px) {
			flex: 0 0 100%;
		}
	}

	@media (max-width: $md3+px) {
		padding: 50px 0px 10px;
	}
}

.box-education {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	&__numbering {
		font-size: 15px;
		font-weight: 300;
		margin: 0px 0px 25px 0px;

		@media (max-width: $md4+px) {
			margin: 0px 0px 15px 0px;
		}
	}

	// .box-education__image
	&__image {
		margin: 0px 0px 35px 0px;

		@media (max-width: $md4+px) {
			margin: 0px 0px 25px 0px;
		}
	}

	// .box-education__title
	&__title {
		font-size: 20px;
		font-weight: 700;
		line-height: 27px;
		margin: 0px 0px 15px 0px;
		text-align: center;

		@media (max-width: $md4+px) {
			margin: 0px 0px 15px 0px;
		}
	}

	// .box-education__text
	&__text {
		p {
			font-size: 15px;
			font-weight: 300;
			text-align: center;
			margin: 0px 0px 15px 0px;

			@media (max-width: $md4+px) {
				margin: 0px 0px 10px 0px;
			}
		}
	}
}

// gallery-items

.gallery-items {
	display: flex;

	@media (max-width: $md3+px) {
		flex-wrap: wrap;
	}

	// .gallery-items__item
	&__item {
		padding: 0px 0px 16.67% 0px;
		width: 100%;

		@media (max-width: $md3+px) {
			flex: 1 0 33.333%;
			padding: 0px 0px 31.67% 0px;
		}
	}
}

// steps

.steps {
	padding: 100px 0px 20px;
	background: url("../../dist/img/decor-img-steps.png") bottom no-repeat;
	max-width: 970px;
	margin: 0px auto;

	// .steps__title
	&__title {
		margin: 0px 0px 105px 0px;

		@media (max-width: $md4+px) {
			margin: 0px 0px 50px 0px;
		}
	}

	// .steps__container
	&__container {}

	// .steps__wrapp
	&__wrapp {
		overflow: hidden;
	}

	// .steps__row
	&__row {
		display: flex;
		flex-wrap: wrap;
		margin: 0px -50px;
	}

	// .steps__column
	&__column {
		flex: 0 0 33.333%;
		padding: 0px 50px;
		margin: 0px 0px 80px 0px;
		position: relative;

		&:after {
			content: '';
			background: url("../../dist/img/icons/right-drawn-arrow-icon.png") center no-repeat;
			display: block;
			width: 64px;
			height: 24px;
			position: absolute;
			top: 19%;
			right: 0;
			transform: translate(50%, 0px);
		}

		@media (max-width: $md3+px) {
			flex: 0 0 50%;
		}

		@media (max-width: $md4+px) {
			flex: 0 0 100%;
			margin: 0px 0px 50px 0px;
		}
	}

	@media (max-width: $md3+px) {
		padding: 50px 0px 80px;
	}
}

.steps-item {}

.item-steps {
	text-align: center;

	// .item-steps__image
	&__image {
		margin: 0px 0px 30px 0px;
	}

	// .item-steps__info
	&__info {
		margin: 0px auto;
		font-size: 16px;
		line-height: calc(22 / 16);
	}

	// .item-steps__btn
	&__btn {
		font-size: 16px;
		color: #001e28;
		font-family: "Circe";
		font-weight: 700;
		border-bottom: 4px solid $decor-bg;
		line-height: 27px;
		display: inline-flex;
		margin: 10px 0px 0px 0px;
	}
}

// license 

.license {
	padding: 100px 0px 40px;
	background: linear-gradient(-211deg, #d8f0f0 0%, #fdfde0 100%);

	// .license__title
	&__title {
		max-width: 775px;
		width: 100%;
		margin: 0px auto 100px auto;

		@media (max-width: $md3+px) {
			margin: 0px auto 50px auto;
		}
	}

	// .license__body
	&__body {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	// .license__item
	&__item {
		flex: 0 0 270px;
		margin: 0px 40px 40px;

		@media (max-width: $md3+px) {
			margin: 0px 20px 40px;
		}

		@media (max-width: $md4+px) {
			margin: 0px 0px 50px;
		}
	}

	// .license__image
	&__image {
		border: 15px solid #cce9e6;
		height: 350px;
		margin: 0px 0px 30px 0px;

		@media (max-width: $md4+px) {
			margin: 0px 0px 15px 0px;
		}
	}

	// .license__name
	&__name {
		font-size: 18px;
		font-weight: 400;
		text-align: center;
	}

	@media (max-width: $md3+px) {
		padding: 50px 0px 30px;
	}
}




//reviews

.reviews {
	background: url("../../dist/img/bg-decor-02.png") top no-repeat;
	padding: 130px 0px 100px;

	// .reviews__container
	&__container {}

	// .reviews__title
	&__title {
		max-width: 670px;
		width: 100%;
		margin: 0px auto;

		.title__item {
			margin: 60px 0px;
			@media (max-width: $md4+px){
			   margin: 50px 0px 25px;
			}
		}
	}

	// .reviews__slider
	&__slider {}

	@media (max-width: $md3+px) {
		padding: 100px 0px 50px;
	}

}

.slider-reviews {

	position: relative;

	&__wrapper {
		max-width: 1050px;
		margin: 0px auto;
	}

	// .slider-reviews__slide
	&__slide {
		margin: 0 40px;
		@media (max-width: $md2+px){
			margin: 0 30px;
		}
		@media (max-width: 890px){
			margin: 0 15px;
		}
		@media (max-width: $md3+px){
			margin: 0 30px;
		}
		@media (max-width: 600px){
			margin: 0 15px;
		}
	}

	// .slider-reviews__conrol
	&__conrol {
	}

	// .slider-reviews__arrow
	&__arrow {
		position: absolute;
		top: 50%;
		display: block;
		width: 25px;
		height: 40px;
		cursor: pointer;
		background: url("../../dist/img/icons/arrow-slider-icon.png") no-repeat;

		// .slider-reviews__arrow_prev
		&_prev {
			transform: translate(0, -50%);
			left: 0;
		}

		// .slider-reviews__arrow_next
		&_next {
			transform: translate(0, -50%) rotate(180deg);
			right: 0;
		}
	}



}







